@font-face {
  font-family: Helvetica World;
  src: url("helvetica-world-regular.e8e9cf94.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: Neo Contact;
  src: url("NeoContact.cb44f5f2.woff2") format("woff2");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

main-layout {
  width: 100%;
  display: block;
}

body {
  color: #222;
  background-color: #fff;
  margin: 0;
  padding: 0;
  font-family: Helvetica World, sans-serif;
  line-height: 1.6;
}

h1, h2, h3, h4, h5 {
  color: #385787;
  margin-bottom: .75rem;
  font-family: Neo Contact, serif;
}

h1 {
  margin-top: .51rem;
  font-size: 4rem;
}

h2 {
  font-size: 3rem;
}

h3 {
  font-size: 2.5rem;
}

h4 {
  font-size: 2rem;
}

h5 {
  font-size: 1rem;
}

p {
  color: #222;
  margin-bottom: 1rem;
  font-size: 1rem;
}

@media (width <= 768px) {
  h1 {
    font-size: 3rem;
  }

  h2 {
    font-size: 2.5rem;
  }

  h3 {
    font-size: 2rem;
  }

  h4 {
    font-size: 1.5rem;
  }

  h5 {
    font-size: 1rem;
  }

  p {
    font-size: .95rem;
  }
}

sl-input::part(base) {
  box-shadow: none;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: .5rem;
  font-family: Helvetica World, sans-serif;
  font-size: 1rem;
  transition: border .2s, box-shadow .2s;
}

sl-input::part(base):focus-within {
  border-color: #385787;
  box-shadow: 0 0 0 2px #3857871a;
}

sl-input.compact::part(base) {
  padding: .4rem .75rem;
  font-size: .95rem;
}

sl-input.error::part(base) {
  border-color: #933023;
  box-shadow: 0 0 0 2px #f003;
}

sl-input::part(label) {
  text-align: left;
  color: #222;
  width: 100%;
  margin-bottom: .5rem;
  font-family: Barlow Semi Condensed, sans-serif;
  font-weight: 600;
  display: block;
}

.form-wrapper {
  background: #fff;
  border-radius: 8px;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 400px;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 8px #0000000d;
}

.form-wrapper form {
  flex-direction: column;
  gap: 15px;
  width: 100%;
  display: flex;
}

.form-wrapper sl-input, .form-wrapper sl-radio-group {
  width: 100%;
  margin-bottom: 1rem;
}

.form-wrapper p {
  text-align: center;
  margin-top: 15px;
}

.form-wrapper a {
  color: #385787;
  text-decoration: none;
}

.form-wrapper a:hover {
  text-decoration: underline;
}

.form-wrapper .radio-label {
  text-align: center;
  margin-bottom: .5rem;
  font-family: Barlow Semi Condensed, sans-serif;
  font-weight: 600;
  display: block;
}

.form-wrapper sl-radio-group::part(base) {
  justify-content: center;
}

.event-card {
  scroll-snap-align: start;
  background: #fff;
  border-radius: 1rem;
  flex-shrink: 0;
  min-width: 400px;
  max-width: 450px;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 10px 20px #0000000d;
}

.event-card:hover {
  transform: translateY(-4px);
}

.event-card img {
  object-fit: cover;
  width: 100%;
  height: 250px;
  margin-bottom: 0;
}

.event-card-body {
  padding: 0 2rem .75rem;
}

.event-card-body h3 {
  color: #385787;
  margin-top: 0;
  font-family: Neo Contact, serif;
}

.button-group {
  gap: 1rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  display: flex;
}

.button-group sl-button {
  flex: 1;
  width: 100%;
}

.button-group sl-button::part(base) {
  width: 100%;
}

@media (width <= 768px) {
  .event-card {
    border-radius: .75rem;
    min-width: 280px;
    max-width: 100%;
    box-shadow: 0 6px 16px #0000000d;
  }

  .event-card-body {
    padding: 1.5rem;
  }

  .event-card-body h3 {
    font-size: 2.5rem;
  }

  .event-card-body p {
    font-size: .85rem;
  }

  .button-group {
    flex-direction: column;
    gap: .75rem;
    margin-top: 1rem;
  }

  .button-group sl-button::part(base) {
    padding: .5rem 1.25rem;
    font-size: .95rem;
  }
}

sl-carousel {
  --slide-gap: 20px;
  padding: 4rem;
  display: flex;
}

sl-carousel::part(slide) {
  justify-content: center;
  display: flex;
}

.event-carousel {
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  gap: 2rem;
  padding: 4rem;
  display: flex;
  overflow-x: auto;
}

.event-carousel::-webkit-scrollbar {
  height: 8px;
}

.event-carousel::-webkit-scrollbar-thumb {
  background: #ccc;
  border-radius: 4px;
}

.event-carousel::-webkit-scrollbar-track {
  background: none;
}

@media (width <= 768px) {
  .event-carousel {
    gap: 1rem;
    padding: 3rem 2rem;
  }
}

sl-dialog::part(panel) {
  background: #fff;
  border-radius: 8px;
  max-width: 600px;
  box-shadow: 0 4px 16px #00000026;
}

sl-dialog::part(title) {
  color: #385787;
  font-family: Barlow Semi Condensed, sans-serif;
  font-size: 1.5rem;
}

sl-dialog::part(body) {
  color: #222;
  padding: 1.5rem;
  font-family: Helvetica World, sans-serif;
}

sl-dialog::part(footer) {
  justify-content: flex-end;
  gap: 1rem;
  padding: 1rem;
  display: flex;
}

.tab-bar {
  overflow-x: auto;
}

.tab-bar .tab {
  color: #bbb;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  background: none;
  border: none;
  border-bottom: 2px solid #0000;
  padding-bottom: .5rem;
  font-family: Barlow Semi Condensed, sans-serif;
  font-size: 1.5rem;
  font-weight: 600;
  transition: all .3s;
}

.tab-bar .tab.active {
  color: #385787;
  border-color: #385787;
}

.tab-bar .tab:hover {
  color: #385787;
  opacity: .8;
}

.toast {
  color: #fff;
  opacity: 0;
  z-index: 9999;
  background: #333;
  border-radius: 6px;
  max-width: 300px;
  padding: .75rem 1.25rem;
  font-size: .95rem;
  transition: opacity .3s, transform .3s;
  position: fixed;
  top: 3rem;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: 0 2px 10px #00000026;
}

.toast.visible {
  opacity: 1;
}

.toast.success {
  background: #385787;
}

.toast.danger {
  background: #933023;
}

.site-header {
  color: #fff;
  background-image: url("header.51bdf215.png");
  background-position: center;
  background-size: cover;
  padding: 20px;
  box-shadow: 0 2px 4px #0000000d;
}

.header-content {
  justify-content: space-between;
  align-items: center;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.logo {
  cursor: pointer;
  margin: 0;
  font-size: 2rem;
}

.nav-tabs {
  --sl-color-primary-600: vars. $ white;
  --sl-color-neutral-0: transparent;
}

.nav-tabs::part(tabs) {
  border-bottom: none;
}

.nav-tabs::part(active-tab-indicator) {
  display: none;
}

sl-tab {
  color: #fff;
  cursor: pointer;
  padding: 10px 20px;
}

sl-tab:hover {
  background-color: #ffffff1a;
}

.container {
  box-sizing: border-box;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1.5rem;
  overflow-x: hidden;
}

.page-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem 0;
}

@media (width <= 768px) {
  .page-content, .page-content .headline {
    text-align: center;
  }

  .page-content .controls-bar {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 1.5rem;
  }

  .page-content .controls-bar .tab-bar {
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
  }

  .page-content .controls-bar sl-button::part(base) {
    width: 100%;
    max-width: 280px;
  }
}

.page-centered {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  max-width: 400px;
  min-height: calc(100vh - 80px);
  margin: 4rem auto;
  padding: 2rem;
  display: flex;
}

.guide-page {
  background: #f6f6f6;
  flex-direction: column;
  min-height: 100vh;
  display: flex;
}

.guide-header {
  background-position: center;
  background-size: cover;
  height: 200px;
}

.guide-content {
  text-align: center;
  flex: 1;
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.guide-text-block {
  margin-bottom: 40px;
}

.guide-title {
  color: #222;
  margin-bottom: 10px;
  font-family: Neo Contact, serif;
  font-size: 2.5rem;
}

.guide-subtitle, .guide-text {
  color: #333;
  font-family: Helvetica World, sans-serif;
  font-size: 1.1rem;
  line-height: 1.6;
}

.guide-heading {
  color: #222;
  margin: 20px 0 10px;
  font-family: Barlow Semi Condensed, sans-serif;
  font-size: 1.8rem;
}

.guide-features {
  background-position: center;
  background-size: cover;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 20px;
  margin-bottom: 40px;
  padding: 20px;
  display: grid;
}

.guide-card {
  text-align: left;
  background: #fff;
  border-radius: 8px;
  padding: 20px;
  box-shadow: 0 2px 8px #0000000d;
}

.guide-card h4 {
  color: #222;
  margin: 0 0 10px;
  font-family: Barlow Semi Condensed, sans-serif;
  font-size: 1.2rem;
}

.guide-card p {
  color: #333;
  margin: 0;
  font-family: Helvetica World, sans-serif;
}

.guide-cta {
  margin: 20px 0;
}

.guide-footer {
  background-position: center;
  background-size: cover;
  height: 100px;
}

@media (width <= 768px) {
  .guide-header {
    height: 150px;
  }

  .guide-content {
    padding: 15px;
  }

  .guide-title {
    font-size: 2rem;
  }

  .guide-heading {
    font-size: 1.5rem;
  }

  .guide-features {
    grid-template-columns: 1fr;
  }

  .guide-footer {
    height: 80px;
  }
}

sl-button::part(base) {
  color: #fff;
  text-transform: uppercase;
  letter-spacing: 1px;
  cursor: pointer;
  background-color: #222;
  border: none;
  border-radius: 999px;
  margin-top: 1rem;
  padding: .5rem 1rem;
  font-family: Barlow Semi Condensed, sans-serif;
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  transition: background-color .3s;
  box-shadow: 0 4px 10px #00000026;
}

sl-button::part(base):hover {
  background-color: #444;
}

sl-button::part(base):active {
  background-color: #385787;
}

sl-button.primary::part(base) {
  --sl-input-height-medium: 48px;
  background-color: #385787;
  font-size: 1.1rem;
  font-weight: 500;
}

sl-button.primary::part(base):hover {
  background-color: #294063;
}

.button-wrapper {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1rem;
  margin-bottom: 2rem;
  display: flex;
}

.button-wrapper sl-button {
  width: 100%;
  max-width: 300px;
}
